html > body {
  font-family: "Cairo", sans-serif;
}

html,
#root,
body {
  height: 100%;
}

body {
  margin: 0px;
}

.App {
  display: flex;
  height: 100%;
  flex-direction: column;
  text-align: center;
}

.SwipeScreen {
  width: 100%;
}

.Question {
  width: 100%;
}

.Image_wrapper {
  width: 100%;
}

img {
  width: 500px;
}

.AcceptRejectButton {
  margin: 30px;
}

.TutorialScreen_Question_Image {
  height: 500px;
  background-size: cover;
}

.TutorialScreen_Question_Image_Text {
  padding: 300px 15% 0 15%;
  color: white;
  text-shadow: 1px 1px 10px black;
}

.EndScreen {
  height: 500px;
  background-size: cover;
}

.EndScreen_Text {
  padding-top: 50px;
  color: white;
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 1px 1px 2px black;
}

.TutorialScreen_Question_Image_Text_Title {
  font-size: 2rem;
  font-weight: bold;
}

.TutorialScreen_Question_Image_Text_Caption {
  font-size: 1.3rem;
}
